.header{
    color: white;
    display: -ms-flexbox;
    display: flex;
    padding: 2rem;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    overflow-x: scroll;
    scrollbar-width: none;
    -ms-overflow-style: none;
}

.header::-webkit-scrollbar{
    display: none;
}

.header__icons{
    -ms-flex: 1 1;
        flex: 1 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    text-align: center;
}

.header > img {
    -o-object-fit: contain;
       object-fit: contain;
    height: 8rem;
}

.header__icons > .header__icon > .MuiSvgIcon-root{
    font-size: 2.6rem !important;
    margin: 0 auto;
}

.header__icon{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 100px;
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
    -ms-flex-align: center;
        align-items: center;
    cursor: pointer;
}

.header__icons > .header__icon > p{
    text-transform: uppercase;
    display: none;
    opacity: 0;
    -webkit-transition: all 0.2s linear;
    -o-transition: all 0.2s linear;
    transition: all 0.2s linear;
}

.header__icon:hover > p,
.header__icon:active > p,
.header__icon__active > p{
    margin-top: 3px;
    display: inline !important;
    opacity: 1 !important;
}



