.movie{
    color: white;
    width: 400px;
    padding: 20px;
    -webkit-transition: all 200ms ease-in;
    -o-transition: all 200ms ease-in;
    transition: all 200ms ease-in;
    line-height: 1.2;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}

.movie:hover{
    -webkit-transform: scale(1.09);
        -ms-transform: scale(1.09);
            transform: scale(1.09);
}

.movie > h1{
    font-weight: 600;
    font-size: 1.8rem;

    padding: 10px 0 0 0;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}

.movie > h2{
    font-weight: 400;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    opacity: 0.7;
    font-size: 1.4rem;
    padding: 5px 0 0 0;
    max-width: 360px;
}

.movie > img{
    width: 360px;
    -o-object-fit: contain;
       object-fit: contain;
}

.movie >div{
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    opacity: 0;
    visibility: hidden;
    -webkit-transition: all 200ms ease-in;
    -o-transition: all 200ms ease-in;
    transition: all 200ms ease-in;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    width: 45%;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.movie:hover > div{
    opacity: 0.7;
    visibility: visible;
}

.movie > div > p{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}


